* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.header {
  z-index: 100;
  width: 100%;
  height: 120px;
  background-color: green;
  justify-content: center;
  padding: 35px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#search-form {
  font: inherit;
  width: auto;
  display: flex;
}

input {
  text-align: center;
  height: 50px;
  width: 250px;
  border: none;
  border-radius: 5px 0 0 5px;
  margin: 0;
  font-size: 27px;
}

button {
  height: 50px;
  width: 80px;
  cursor: pointer;
  background-color: #5ea2a7;
  border: none;
  border-radius: 0 5px 5px 0;
  margin: 0;
  padding: 0;
  font-size: 17px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 140px;
  padding: 30px;
  display: flex;
}

.image {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.photo-card {
  max-width: 250px;
  min-height: 208px;
  border-radius: 4px;
  flex-basis: calc(25% - 50px);
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
  box-shadow: 0 4px 16px rgba(0, 0, 0, .25);
}

.photo-card:hover {
  transform: scale(1.1);
}

.info {
  justify-content: space-between;
  padding: 0 5px;
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 10px;
}

